@use "./common/mixins";
@use "./common/global";

.extention {
  display: flex;
  width: 280px;
  flex-direction: column;
  align-items: center;
  padding: 25px 0;
  margin: 0 auto;

  @media (min-width: global.$tablet-width) {
    width: 100%;
    padding: 0;
  }

  @media (min-width: global.$desktop-width) {
    width: 169px;
    padding: 0;
  }
}

.extention__action {
  @include mixins.oswald(16px, 20px);
  position: relative;
  padding-top: 53px;
  text-transform: uppercase;
  color: #111111;
  margin: 0;
  margin-bottom: 8px;
  text-align: center;

  &::before {
    display: block;
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    top: -15px;
    left: 90px;
    background-image: url("../../img/plus.svg");
  }

  @media (min-width: global.$tablet-width) {
    &::before {
      width: 60px;
      height: 60px;
      top: -65px;
      left: 75px;
      background-image: url("../../img/plus.svg");
      background-size: cover;
    }
  }

  @media (min-width: global.$desktop-width) {
    &::before {
      top: -70px;
      left: 55px;
    }
  }
}

.extention__promise {
  @include mixins.lato(12px, 16px);
  margin: 0;
  margin-bottom: 21px;
  text-align: center;
}

.extention__link {
  width: 100%;
  margin: 0 20px;
  background-color: #f2f2f2;
  color: #444444;

  @media (min-width: global.$tablet-width) {
    margin: 0;
  }
}
